html{
    background-color: rgb(104, 178, 203);
    font-family:Arial, Helvetica, sans-serif;
    font-weight:bold;
  }
  header{
    color:rgb(25,82,100);
    color:black;
    text-align:center;
    font-size: 30px;
    font-weight:bold;
    margin-top:130px;
  }
  #drum-machine{
    background-color: rgb(200, 227, 236);
    width:600px;
    position: absolute;
    top: 50%;
    left:50%;
    transform: translate(-50%, -50%);
  }
  #drum-machine-box{
    display:flex;
    flex-direction:row;
    justify-content:center;
    align-items:center;
  
    border:solid rgb(148,153,155) 5px;
  }
  button{
    font-weight:bold;
    display:inline-block;
    background-color: rgb(184,186,187);
    width:90px;
    height:70px;
    border:none;
    border-radius:6px;
    color: inherit;
      padding:0;
    margin:7px;
      font: inherit;
      cursor: pointer;
      outline: inherit;
    box-shadow: 3px 3px 3px grey;
    text-shadow: 0px 0px 0px transparent;
  }
  #pad-panel{
    margin:40px;
    display:grid;
    grid-template-columns:repeat(3, auto);
    grid-template-rows:repeat(3, auto);
    justify-self:center;
    align-self:center;
  }
  #display{
    display:flex;
    flex-direction:column;
  }
  #display-content{
    text-align:center;
    margin:15px;
    line-height:30px;
    background-color:rgb(184,186,187);
  }
  #bank{
    margin:15px;
    text-align:center;
  }
  /* The switch - the box around the slider */
  .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  
  